const rotateImg = keyframes`
    0% {
        transform: perspective(1000px) rotateY(0deg);
    }

    100% {
        transform: perspective(1000px) rotateY(360deg);
    }
`;

export const UniTokenAnimated = styled.img`
    animation: ${rotateImg} 5s cubic-bezier(0.83, 0, 0.17, 1) infinite;
    padding: 2rem 0 0 0;
    filter: drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.15));
`;

function handleClickExternalLink(event: React.MouseEvent<HTMLAnchorElement>) {
    const { target, href } = event.currentTarget;

    const anonymizedHref = anonymizeLink(href);

    // don't prevent default, don't redirect if it's a new tab
    if (target === "_blank" || event.ctrlKey || event.metaKey) {
        // ReactGA.outboundLink({ label: anonymizedHref }, () => {
        //   window.location.href = anonymizedHref
        // })
    } else {
        event.preventDefault();
        // ReactGA.outboundLink({ label: anonymizedHref }, () => {
        //   window.location.href = anonymizedHref
        // })
    }
}

/**
 * Outbound link that handles firing google analytics events
 */
export function ExternalLink({ target = "_blank", href, rel = "noopener noreferrer", ...rest }: Omit<HTMLProps<HTMLAnchorElement>, "as" | "ref" | "onClick"> & { href: string }) {
    return <a className={"flex-s-between fs-085"} target={target} rel={rel} href={href} onClick={handleClickExternalLink} {...rest} />;
}

export function ExternalLinkIcon({ target = "_blank", href, rel = "noopener noreferrer", ...rest }: Omit<HTMLProps<HTMLAnchorElement>, "as" | "ref" | "onClick"> & { href: string }) {
    return (
        <LinkIconWrapper target={target} rel={rel} href={href} onClick={handleClickExternalLink} {...rest}>
            <LinkIcon />
        </LinkIconWrapper>
    );
}

const rotate = keyframes`
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
`;

const Spinner = styled.img`
    animation: 2s ${rotate} linear infinite;
    width: 16px;
    height: 16px;
`;

const BackArrowLink = styled(StyledInternalLink)`
    color: ${({ theme }) => "var(--text1)"};
`;

export function BackArrow({ to }: { to: string }) {
    return (
        <BackArrowLink to={to}>
            <ArrowLeft />
        </BackArrowLink>
    );
}

